.l-header {
	a {
		text-decoration: none;
	}
	.header-wrapper {
		.containerTop {
			background-color: #efefef;
			padding: 10px 0;
			.inner {
				width: 1120px;
				margin: 0 auto;
				h1 {
					font-size: 12px;
					line-height: 1.4em;
					letter-spacing: 1px;
				}
			}
		}
		.containerMiddle {
			padding: 30px 0;
			.inner {
				width: 1120px;
				margin: 0 auto;
				display: flex;
				justify-content: space-between;
				align-items: center;
				.box-left {
					display: flex;
					align-items: center;
					.logo {
						width: 310px;
						margin-right: 30px;
						img {
							width: 100%;
							image-rendering: -webkit-optimize-contrast;
						}
					}
					.group-button {
						font-weight: 700;
						font-size: 1.5rem;
						line-height: 1.43;
						letter-spacing: 1px;
						list-style: none;
						li {
							// color: #fff;
							// color: #e45213;
							position: relative;
							padding-left: 18px;
							margin-bottom: 3px;
							&::before {
								content: '';
								display: block;
								width: 8px;
								height: 8px;
								background-color: #e45213;
								border-radius: 50%;
								position: absolute;
								top: 50%;
								left: 0;
								transform: translateY(-50%);
							}
							span {
								background:linear-gradient(transparent 35%, #f6ee0f 75%);
							}
							&:last-child {
								margin-bottom: 0;
							}
						}
					}
				}
				.box-right {
					ul {
						list-style: none;
						display: flex;
						justify-content: space-between;
						.phone {
							margin-right: 20px;
							p {
								font-size: 1.3rem;
								font-weight: bold;
								text-align: center;
								margin-bottom: 10px;
							}
							a {
								position: relative;
								padding-left: 30px;
								text-decoration: none;
								color: #333;
								display: block;
								font-size: 2.8rem;
								font-weight: bold;
								margin-bottom: 8px;
							}
							a.phoneLink:before {
								content: "";
								width: 33px;
								height: 22px;
								background: url(../img/phone-alt-solid.svg) no-repeat;
								background-size: auto 100%;
								left: 0;
								top: 50%;
								position: absolute;
								transform: translateY(-50%);
							}
							span {
								line-height: 1em;
								font-size: 1rem;
							}
						}
						.mail {
							background-color: #e45213;
							border-radius: 40px;
							overflow: hidden;
							a {
								position: relative;
								display: flex;
								justify-content: center;
								align-items: center;
								padding: 10px 40px;
								font-size: 1.6rem;
								font-weight: bold;
								line-height: 1.5;
								letter-spacing: 1px;
								color: #fff;
								&::after {
									position: absolute;
									top: 50%;
									right: 20px;
									content: '';
									display: block;
									width: 8px;
									height: 8px;
									border-top: 3px solid #fff;
									border-left: 3px solid #fff;
									transform: translateY(-50%) rotate(135deg);
								}
							}
						}
					}
				}
			}
		}
		#header-nav {
			background-color: #70c438;
			nav {
				width: 1000px;
				margin: 0 auto;
				ul {
					display: flex;
					justify-content: space-between;
					border-right: 1px solid #fff;
					li {
						width: 100%;
						border-left: 1px solid #fff;
						a {
							display: block;
							text-decoration: none;
							color: #fff;
							width: 100%;
							height: 70px;
							text-align: center;
							line-height: 70px;
							font-weight: bold;
							font-size: 1.6rem;
						}
					}
				}
			}
		}
	}
}